import axios from 'axios';
export const ReportTypePaths = {
    ["cost" /* cost */]: 'reports/openshift/infrastructures/all/costs/',
    ["cpu" /* cpu */]: 'reports/openshift/compute/',
    ["database" /* database */]: 'reports/openshift/infrastructures/all/costs/',
    ["instance_type" /* instanceType */]: 'reports/openshift/infrastructures/all/instance-types/',
    ["memory" /* memory */]: 'reports/openshift/memory/',
    ["network" /* network */]: 'reports/openshift/infrastructures/all/costs/',
    ["storage" /* storage */]: 'reports/openshift/infrastructures/all/storage/',
    ["volume" /* volume */]: 'reports/openshift/volumes/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
