import { getQueryRoute } from 'api/queries/query';
export const baseQuery = {
    filter: {
        time_scope_units: 'month',
        time_scope_value: -1,
        resolution: 'daily',
    },
};
export const getRouteForQuery = (history, query, reset = false) => {
    // Reset pagination
    if (reset) {
        query.filter = Object.assign(Object.assign({}, query.filter), { offset: baseQuery.filter.offset });
    }
    return `${history.location.pathname}?${getQueryRoute(query)}`;
};
export const isOcpAvailable = (ocpProviders, ocpProvidersFetchStatus, userAccess) => {
    let result = false;
    if (ocpProvidersFetchStatus === 2 /* complete */) {
        const data = userAccess.data.find(d => d.type === "ocp" /* ocp */);
        const isUserAccessAllowed = data && data.access;
        // providers API returns empty data array for no sources
        result =
            isUserAccessAllowed &&
                ocpProviders !== undefined &&
                ocpProviders.meta !== undefined &&
                ocpProviders.meta.count > 0;
    }
    return result;
};
