import axios from 'axios';
// Todo: update to use ibm backend apis when they become available
export const ReportTypePaths = {
    ["cost" /* cost */]: 'reports/gcp/costs/',
    ["database" /* database */]: 'reports/gcp/costs/',
    ["network" /* network */]: 'reports/gcp/costs/',
    ["storage" /* storage */]: 'reports/gcp/storage/',
    ["instance_type" /* instanceType */]: 'reports/gcp/instance-types/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
