import { getReportId, stateKey, userAccessKey } from './userAccessCommon';
export const selectUserAccessState = (state) => state[stateKey];
// Add provider
export const selectAddProviderFetchStatus = (state) => selectUserAccessState(state).fetchStatus.get(userAccessKey);
export const selectAddProviderError = (state) => selectUserAccessState(state).errors.get(userAccessKey);
// Fetch userAccess
export const selectUserAccess = (state, providerType, query) => selectUserAccessState(state).byId.get(getReportId(providerType, query));
export const selectUserAccessFetchStatus = (state, providerType, query) => selectUserAccessState(state).fetchStatus.get(getReportId(providerType, query));
export const selectUserAccessError = (state, providerType, query) => selectUserAccessState(state).errors.get(getReportId(providerType, query));
