import { addProviderKey, getReportId, stateKey } from './providersCommon';
export const selectProvidersState = (state) => state[stateKey];
// Add provider
export const selectAddProviderFetchStatus = (state) => selectProvidersState(state).fetchStatus.get(addProviderKey);
export const selectAddProviderError = (state) => selectProvidersState(state).errors.get(addProviderKey);
// Fetch providers
export const selectProviders = (state, providerType, query) => selectProvidersState(state).byId.get(getReportId(providerType, query));
export const selectProvidersFetchStatus = (state, providerType, query) => selectProvidersState(state).fetchStatus.get(getReportId(providerType, query));
export const selectProvidersError = (state, providerType, query) => selectProvidersState(state).errors.get(getReportId(providerType, query));
