import { getType } from 'typesafe-actions';
import { fetchReportFailure, fetchReportRequest, fetchReportSuccess } from './reportActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function reportReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchReportRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.reportId, 1 /* inProgress */) });
        case getType(fetchReportSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.reportId, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.reportId, null) });
        case getType(fetchReportFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.reportId, action.payload) });
        default:
            return state;
    }
}
